<template>
  <div id="categories">
    <div class="list">
      <div
        class="item"
        v-for="(i, k) in categories"
        :key="k"
        :class="[isCategory === i.name ? 'active' : '']"
        @click="selectCategory(i.name)"
      >
        {{ i.title }}
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Categories',
  props: ['isCategory'],
  data () {
    return {
      categories: [
          {
              title: 'Headphone',
              name: 'head-phone'
          },
          {
              title: 'Headband',
              name: 'head-band'
          },
          {
              title: 'Earpads',
              name: 'head-earpads'
          },
          {
              title: 'Headphone1',
              name: 'head-phone1'
          },
          {
              title: 'Headband2',
              name: 'head-band2'
          },
          {
              title: 'Earpads3',
              name: 'head-earpads3'
          },
      ]
    }
  },
  methods: {
    selectCategory (category) {
      this.$emit('onSelectCategory', category)
    }
  }
}
</script>

<style lang="scss" scoped>
#categories {
  padding: 12px 0;
}
.list {
  display: flex;
  align-items: center;
  grid-gap: 12px;
  overflow-x: auto;
  &::-webkit-scrollbar {
    display: none;
  }
  .item {
    padding: 6px 12px;
    flex-shrink: 0;
    flex-grow: 0;
    background-color: transparent;
    color: $color-grey-dark;
    font-size: 14px;
    line-height: 20px;
    font-weight: 400;
    border-radius: 30px;
    transition: all ease 0.3s;
    cursor: pointer;
    &.active,
    &:hover {
      color: $color-white;
      background-color: $color-primary;
    }
  }
}
</style>