import Vue from 'vue'
import Vuex from 'vuex'

import { auth, db } from '@/firebase/config'
import { onAuthStateChanged } from "firebase/auth";
import { collection, getDocs, doc, getDoc } from "firebase/firestore"


Vue.use(Vuex)

const store = new Vuex.Store({
    state: {
        admin: null,
        isLogin: false,
        products: [],
        productByID: {}
    },
    mutations: {
        setAdmin: (state, payload) => state.admin = payload,
        setIsLogin: (state, payload) => state.isLogin = payload,
        setProducts: (state, payload) => state.products = payload,
        setProductByID: (state, payload) => state.productByID = payload
    },
    actions: {
        login (context, payload) {
            context.commit('setAdmin', payload)
        },
        // Get Products
        async getProducts ({ commit }) {
            const data = []
            const querySnapshot = await getDocs(collection(db, "products"))
            await querySnapshot.forEach((doc) => {
                data.push({ id: doc.id, ...doc.data() })
            })
            commit('setProducts', data)
        },
        // Get Product By ID
        async getProductByID ({ commit }, id) {
            const docRef = doc(db, 'products', id)
            const docSnap = await getDoc(docRef)
            commit('setProductByID', docSnap.data())
        },
    },
    modules: {
    }
})

onAuthStateChanged(auth, (user) => {
    if (user) {
        store.commit('setIsLogin', true)
        store.commit('setAdmin', user)
    } else {
        store.commit('setIsLogin', false)
        store.commit('setAdmin', null)
    }
});

export default store