<template>
  <div id="product-list">
    <div class="container">
      <Categories :isCategory="isCategory" @onSelectCategory="SelectedCategory"/>
      <ProductSection :title="title1" :data="filterProductsByCategory" />
      <ProductSection :title="title2" :data="products" />
    </div>
  </div>
</template>

<script>
import Categories from '@/components/Home/Categories.vue'
import ProductSection from '@/components/Home/ProductSection.vue'
import { mapActions, mapState } from 'vuex'

export default {
  name: 'ProductList',
  components: {
    Categories,
    ProductSection
  },
  data () {
      return {
          title1: 'Featured Headphone',
          title2: 'All',
          isCategory: 'head-phone'
      }
  },
  computed: {
    ...mapState(['products']),
    filterProductsByCategory () {
        return this.products.filter(i => i.category === this.isCategory)
    }
  },
  methods: {
    ...mapActions(['getProducts']),
    SelectedCategory (category) {
        this.isCategory = category
    }
  },
  created () {
      this.getProducts()
  }
}
</script>

<style lang="scss" scoped>
#product-list {
  background-color: $color-grey-light-1;
  padding: 24px 0;
  border-radius: 30px 30px 0px 0px;
}
</style>